<template>
  <Teleport to="body">
    <button @click="goTop" :class="{ 'show': isScrolled }" class="go-top" type="button" title="Go Top">Go Top</button>
  </Teleport>
</template>

<script>
  export default {
    data() {
      return {
        isScrolled: false,
      };
    },
    methods: {
      goTop(){
        window.scrollTo({
          top: 0,
          behavior: 'smooth',
        });
      },
      handleScroll() {
        if (window.scrollY > 200) {
          this.isScrolled = true;
        } else {
          this.isScrolled = false;
        }
      }
    },
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    beforeDestroy() {
      window.removeEventListener('scroll', this.handleScroll);
    },
  }
</script>

<style lang="scss" scoped>
.go-top{
  position: fixed;
  right: 30px;
  bottom: -10px;
  width: 40px;
  height: 40px;
  background: #ffd93d;
  border-radius: 50%;
  font-size: 0;
  border: 2px solid #4f200d;
  opacity: 0;
  pointer-events: none;
  transform: scale(.5);
  transition: .2s ease-in-out;
  z-index: 100;
  &.show{
    opacity: 1;
    pointer-events: auto;
    bottom: 30px;
    transform: scale(1);
  }
  &::after{
    content: '';
    display: block;
    width: 12px;
    height: 12px;
    border-top: 2px solid #4f200d;
    border-right: 2px solid #4f200d;
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%) rotate(-45deg);
  }
}
@media (min-width: 992px) {
  .go-top:hover{ background: #fbe27d; }
}
@media (max-width: 767px) {
  .go-top{
    width: 35px;
    height: 35px;
    right: 25px;
    &.show{ bottom: 25px; }
    &::after{
      width: 10px;
      height: 10px;
    }
  }
}
</style>